@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  overflow-x: hidden;
}
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  font-family: "Open Sans Condensed", sans-serif;
  background-color: #101316;
  color: #fff;
}
li {
  list-style: none;
}
._container {
  max-width: 1172px;
  margin: 0 auto;
}
._title {
  font-weight: bold;
  font-size: 25px;
  line-height: 34px;
  letter-spacing: 0.055em;
  text-transform: uppercase;
  color: inherit;
  margin-bottom: 30px;
}

.header__item{font-weight:300}.header__item a{line-height:25px}.header__item:nth-child(1),.header__item:nth-child(3){font-size:15px;color:#fff}.header__item:nth-child(1){margin-right:200px}.header__item:nth-child(2){font-size:30px;margin-bottom:15px;position:absolute}.header__ul{padding:36px 0px 16px 0px;align-items:center;justify-content:center;display:flex}.header__bullets{display:flex;align-items:center;justify-content:center;padding:0px 0px 19px 0px}.header_line{border-bottom:#5e5757 solid 1px}.bullets__item{width:15px;height:15px;border-radius:50%}.bullets__item:not(:last-child){margin-right:9px}
