-hover: hover){.swiper-button-prev:hover{background-color:#fff}}@media(max-width: 645px){.swiper-button-prev{display:none}}.swiper-button-next{cursor:pointer;position:absolute;right:0;top:50%;margin-top:-25.5px;min-width:51px;height:51px;margin-left:22px;border:solid 1px #fff;border-radius:50%}@media(any-hover: hover){.swiper-button-next:hover{background-color:#fff}}@media(max-width: 645px){.swiper-button-next{display:none}}
