.swiper-container {
    max-width: 1172px;
    @media (max-width: 1330px) {
        max-width: 920/1330 * 100%;
    }
    @media (max-width: 645px) {
        max-width: 100%;
    }
    position: static;
    margin: 0 auto;
}
.works > h2 {
    @media (max-width: 1330px) {
        max-width: 920/1330 * 100%;
    }
    @media (max-width: 645px) {
        margin-left: 0;
    }
}
.slider__container {
    margin: 0 auto;
    position: relative;
    max-width: 1318px;
    @media (max-width: 1330px) {
        max-width: 1100/1330 * 100%;
    }
    @media (max-width: 645px) {
        max-width: 100%;
    }
}
.works {
    @media (max-width: 645px) {
        margin-left: 12px;
    }
    padding-top: 55px;
    &__slide {
        display: flex;
        flex-direction: column;
        max-width: 275px;
        max-height: 454px;
        width: 100%;
        color: #ffffff;
        padding: 0 46px;
        margin: 0 auto;
        border-radius: 5px;
        background-color: #d12d2d;
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.32);
    }

    &__slider-btn {
    }
}
.slide-item {
    &__image {
        margin-top: 44px;
        position: relative;
        min-width: 30%;
        padding: 0px 0px 180px 0px;
        img {
            position: absolute;
            width: 100%;
            height: 100%;
            border-radius: 6px;
            object-fit: cover;
            top: 0;
            left: 0;
        }
    }

    &__content {
        font-weight: bold;
        margin: 0 auto;
        margin-top: 26px;
        margin-bottom: 44px;
        min-width: 279px;
        min-height: 160px;
        & div:not(:last-child) {
            margin: 0px 0px 11px 0px;
        }
    }

    &__title {
        display: inline;
        color: inherit;
        transition: color 0.3s ease 0s;
        font-size: 20px;
        line-height: 27px;
        text-transform: uppercase;
        margin: 0px 0px 11px 0px;
        @media (any-hover: hover) {
            &:hover {
                color: #66f095;
            }
        }
    }

    &__year {
        font-size: 18px;
        line-height: 25px;
    }

    &__skills {
        font-size: 18px;
        line-height: 25px;
    }

    &__text {
        font-size: 18px;
        line-height: 25px;
    }
}
.slide-item-skills {
    &__skill {
        color: #8695a4;
        transition: color 0.3s ease 0s;
        &:not(:last-child) {
            &::after {
                content: ", ";
            }
        }
        @media (any-hover: hover) {
            &:hover {
                &::after {
                    color: #8695a4;
                }
                color: #66f095;
            }
        }
    }
}
.swiper-button-prev {
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -25.5px;
    min-width: 51px;
    height: 51px;
    margin-right: 22px;
    border: solid 1px #fff;
    border-radius: 50%;
    @media (any-hover: hover) {
        &:hover {
            background-color: #fff;
        }
    }
    @media (max-width: 645px) {
        display: none;
    }
}
.swiper-button-next {
    @media (any-hover: hover) {
        &:hover {
            background-color: #fff;
        }
    }
    @media (max-width: 645px) {
        display: none;
    }
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -25.5px;
    min-width: 51px;
    height: 51px;
    margin-left: 22px;
    border: solid 1px #fff;
    border-radius: 50%;
}
